import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { CateringOrder } from '../../../../../../services/data/catering/catering-order.class';
import { CateringItem } from '../../../../../../services/data/catering/catering-item.class';
import { AppService } from '../../../../../../services/app.service';

import * as dayjs from 'dayjs';

@Component({
    selector: 'catering-order-list-item',
    templateUrl: './item.component.html',
    styleUrls: ['./item.component.scss']
})
export class BookingCateringOrderItemComponent implements OnInit {
    /** Catering order to display */
    @Input() public order: CateringOrder;
    /** Emitter for edit events */
    @Output() public edit = new EventEmitter<void>();
    /** Emitter for delete events */
    @Output() public delete = new EventEmitter<void>();

    public get expired(): boolean {
        if (!this.order) return false;
        const time =
            (this.order.changes ? this.order.changes.delivery_time : null) ||
            this.order.delivery_time;
        return dayjs().isAfter(dayjs(this.order.booking_date).add(time, 'm'), 's');
    }

    constructor(private _service: AppService) { }

    /** Display string for the delivery location of the order */
    public get location(): string {
        if (!this.order) return 'No Location';
        if (this.order.changes && this.order.changes.location_id) {
            const space = (this._service.Rooms.list(true) || []).find(room => room.id = this.order.changes.location_id) || {};
            return space.name;
        }
        return this.order.location;
    }

    /** Display string for the set delivery time */
    public get time(): string {
        if (!this.order) return 'No Delivery Time';
        const duration =
            (this.order.changes ? this.order.changes.delivery_time : null) ||
            this.order.delivery_time;
        return dayjs(this.order.booking_date).add(duration, 'm').format('h:mm A');
    }

    /** Number of items in the order */
    public get count(): number {
        if (!this.order) return 0;
        const items: CateringItem[] =
            (this.order.changes ? this.order.changes.items : null) || this.order.items;
        return (items || []).reduce((total, item) => total + item.amount, 0);
    }

    /** Total cost of the items in the order */
    public get total(): number {
        if (!this.order) return 0;
        const items: CateringItem[] =
            (this.order.changes ? this.order.changes.items : null) || this.order.items;
        return (items || []).reduce((total, item) => total + item.total, 0);
    }

    /** Current symbol for the order */
    public get symbol(): string {
        if (!this.order) return 'USD';
        const space = this._service.Rooms.list(true).find(room => room.id === this.order.location_id);
        if (!space) return 'USD';
        const building = this._service.Buildings.get(space.level.bld_id) || {} as any;
        return building.currency || 'USD';
    }

    public ngOnInit(): void {}
}
