
import { trigger, state, style, animate, transition } from '@angular/animations';
import { Component, Input, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { FormControl } from '@angular/forms';

import { BaseComponent } from '../../../../../../shared/globals/base.component';
import { IRoom } from '../../../../../../services/data/rooms.service';
import { AppService } from '../../../../../../services/app.service';
import { CateringCategory } from '../../../../../../services/data/catering/catering-category.class';
import { CateringItem } from '../../../../../../services/data/catering/catering-item.class';

@Component({
    selector: 'catering-item',
    templateUrl: './list-item.component.html',
    styleUrls: ['./list-item.component.scss'],
    animations: [
        trigger('show', [
            state('show', style({ opacity: 1, height: '*' })),
            state('hide', style({ opacity: 0, height: 0 })),
            transition('show <=> hide', animate('200ms')),
        ])
    ]
})
export class BookingMainFlowCateringListItemComponent extends BaseComponent {
    /** Simple display of details */
    @Input() simple: boolean;
    /** Space selected to add catering to */
    @Input() public space: IRoom;
    /** Catering parent group or category */
    @Input() public parent: CateringCategory;
    /** Catering item or item group */
    @Input() public item: CateringCategory;
    /** Whether booking is within the catering exclusion period */
    @Input() public exclude: boolean;
    /** Field for storing ordered catering items */
    @Input() public items: FormControl;
    /** Whether the item can be selected */
    @Input() public disabled: number;
    /** Step value for adding catering items */
    @Input() public step: number = 1;
    /** Emitter for changes to the selected amount of the item */
    @Output() public amountChange = new EventEmitter<number>();

    /** Whether to show list of sub items */
    public show_list: boolean;

    constructor(private _service: AppService) {
        super();
    }

    /** Currency code for the active space */
    public get symbol(): string {
        const level = this.space.level || {} as any;
        const building = this._service.Buildings.get(level.bld_id) || {}  as any;
        return building.currency || 'USD';
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.items && this.items) {
            this.subscription('control', this.items.valueChanges.subscribe(() => this.updateAmounts()));
        }
        if (changes.space && this.space) {
            this.updateAmounts()
        }
    }

    /**
     * Add the given item from the catering order
     * @param item Item to add
     */
    public add(new_item: CateringItem | CateringCategory) {
        if (this.items) {
            const items: CateringItem[] = this.items.value || [];
            if (!items.find(item => item.id === new_item.id)) {
                items.push(new_item instanceof CateringCategory ? new CateringCategory(new_item) : new CateringItem(new_item));
            }
            const item = items.find(item => item.id === new_item.id);
            item.addToOrder();
            this.items.setValue(items);
            this.amountChange.emit(item.amount);
        }
    }

    /**
     * Remove the given item from the catering order
     * @param item Item to remove
     */
    public remove(existing_item: CateringItem | CateringCategory) {
        if (this.items && this.space) {
            const items: CateringItem[] = this.items.value || [];
            const item = items.find(item => item.id === existing_item.id);
            if (item) {
                item.removeFromOrder();
                if (item.amount === 0) {
                    items.splice(items.indexOf(item), 1);
                    this.items.setValue(items);
                }
                this.updateAmounts();
                this.amountChange.emit(item.amount);
            }
        }
    }

    /**
     * Toggle the activation state of the item
     */
    public toggle() {
        if (this.item.amount === 0) {
            this.add(this.item);
        } else {
            this.remove(this.item);
        }
    }

    public handleSelect(item: CateringItem | CateringCategory) {
        if (this.item instanceof CateringCategory) {
            if (this.item.must_select === 1) {
                for (const i of this.item.items) {
                    if (i.amount === 0) {
                        i.addToOrder();
                    }
                }
            } else if (this.item.must_select > 1 && item.amount > 0) {
                const active = this.item.items.reduce((v, i) => v + (i.amount > 0 ? 1 : 0), 0);
                if (active > this.item.must_select) {
                    let diff = active - this.item.must_select;
                    for (const i of this.item.items) {
                        if (diff > 0 && i.amount > 0 && !(item.id === i.id || item.name === i.name)) {
                            this.item.setAmount(0);
                            diff--;
                        }
                    }
                }
            }
        }
    }

    public updateAmounts() {
        if (this.items) {
            const items: CateringItem[] = this.items.value || [];
            const item = items.find(item => this.item.id === item.id);
            if (item && item !== this.item && item.amount !== this.item.amount) {
                this.item.setAmount(item.amount);
            } else {
                this.item.setAmount(0);
            }
        }
    }
}
