import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { ADynamicFormField } from '@acaprojects/ngx-dynamic-forms';
import { FormGroup } from '@angular/forms';

import { AppService } from '../../../services/app.service';
import { IBooking } from '../../../services/data/bookings.service';
import { IRoom } from '../../../services/data/rooms.service';
import { IUser } from '../../../services/data/users.service';
import { BaseComponent } from '../../../shared/globals/base.component';
import { generateBookingFormMetadata } from '../../../shared/utilities/booking.utilities';
import { formatSpaces } from '../../../shared/utilities/formatting.utilities';
import { ICateringOrderList } from './catering/catering-order/catering-order.component';
import { CateringOrder } from '../../../services/data/catering/catering-order.class';

import { BookingEquipmentDetailsModalComponent } from '../overlays/equipment-details/equipment-details.component';
import { BookingCateringDetailsModalComponent } from '../overlays/catering-details/catering-details.component';
import { BookingDetailsModalComponent } from '../overlays/booking-details/booking-details.component';

import * as dayjs from 'dayjs';

export interface IBookingFlowEvent {
    type: 'next' | 'previous';
    data?: { [name: string]: any };
}

@Component({
    selector: 'booking-main-flow',
    templateUrl: './main-flow.component.html',
    styleUrls: ['./main-flow.component.scss']
})
export class BookingMainFlowComponent extends BaseComponent implements OnInit {
    /** Booking ID */
    public id: string;
    /** List of form fields associated with a booking */
    public form_fields: ADynamicFormField[];
    /** Name of the current page shown on the booking flow */
    public page: string;
    /** Form field for listing selected catering items */
    public catering: ADynamicFormField;
    /** Form field for the list of selected spaces */
    public space_list: ADynamicFormField;
    /** Form field for the selected date */
    public date_field: ADynamicFormField;
    /** Active catering order */
    public order: CateringOrder;

    constructor(
        private _service: AppService,
        private _route: ActivatedRoute,
        private _router: Router
    ) {
        super();
    }

    public ngOnInit(): void {
        if (!this._service.ready()) {
            return this.timeout('init', () => this.ngOnInit());
        }
        this.subscription(
            'route',
            this._route.paramMap.subscribe(params => {
                if (params.has('page')) {
                    this.page = params.get('page');
                }
            })
        );
        this.subscription(
            'route_change',
            this._router.events.subscribe(event => {
                if (event instanceof NavigationEnd && event.url.indexOf('book') < 0) {
                    this.clearForm();
                }
            })
        );
        this.loadForm();
    }

    /** List of selected rooms for the booking */
    public get spaces(): IRoom[] {
        const field = this.form_fields.find(i => i.key === 'room');
        if (field) {
            return field.control.value || [];
        }
        return [];
    }

    /** Timestamp of the end of the recurrence period */
    public get recurr_end(): number {
        let field = this.form_fields.find(i => i.key === 'recurrence');
        if (field) {
            return (field.control.value || { end: 0 }).end || 0;
        }
        field = this.form_fields.find(i => i.key === 'period_group');
        if (field) {
            return ((field.control as FormGroup).controls.recurrence.value || { end: 0 }).end || 0;
        }
        return 0;
    }

    /** Datetime selected for the booking */
    public get date(): number {
        const field = this.date_field;
        if (field) {
            return field.control.value;
        }
        return dayjs()
            .startOf('m')
            .valueOf();
    }

    /** Selected duration for the booking */
    public get duration(): number {
        let field: any = this.form_fields.find(i => i.key === 'duration');
        if (field) {
            return field.control.value;
        }
        field = this.form_fields.find(i => i.key === 'time_group');
        if (field) {
            return field.control.controls.duration.value;
        }
        return 60;
    }

    /** Whether booking is all day */
    public get all_day(): boolean {
        const field = this.form_fields.find(i => i.key === 'all_day');
        return field ? field.control.value : false;
    }

    /** Whether the user want to add catering to their booking */
    public get needs_catering(): boolean {
        const empty = { control: { value: {} } };
        const catering: boolean = (this.form_fields.find(i => i.key === 'needs_catering') || empty)
            .control.value;
        return catering;
    }

    /** Number of items in the catering order */
    public get has_ordered(): number {
        const empty = { control: { value: {} } };
        const order: ICateringOrderList = (
            this.form_fields.find(i => i.key === 'catering') || empty
        ).control.value;
        return order
            ? Object.keys(order).reduce((v, key) => {
                  const room_order = order[key];
                  if (room_order && room_order.items) {
                      v += room_order.items.reduce((c, i) => c + (i.amount || 0), 0);
                  }
                  return v;
              }, 0)
            : 0;
    }

    /**
     * Progress to the next step in the flow
     */
    public progress(event: IBookingFlowEvent) {
        const empty = { control: { value: true } };
        const catering = (this.form_fields.find(i => i.key === 'needs_catering') || empty).control
            .value;
        const space = (this.form_fields.find(i => i.key === 'needs_space') || empty).control.value;
        const id = (this.form_fields.find(i => i.key === 'id') || empty).control.value;
        this.id = id || !space ? '10' : '';
        if (event.type === 'next') {
            switch (this.page) {
                case 'catering':
                    this._service.navigate(['book', 'main', 'orders']);
                    const order_list: CateringOrder[] = this.catering.getValue() || [];
                    const index = order_list.findIndex(order => order.id === this.order.id);
                    const new_item_count = this.order.changes.items
                        ? this.order.changes.items.reduce(
                              (count, item) => count + (item.amount || 0),
                              0
                          )
                        : this.order.item_count;
                    if (new_item_count > 0 && index < 0) {
                        order_list.push(new CateringOrder(this.order.toJSON(true)));
                    } else {
                        index >= 0 && new_item_count > 0
                            ? order_list.splice(index, 1, new CateringOrder(this.order.toJSON(true)))
                            : index >= 0
                                ? order_list.splice(index, 1)
                                : '';
                    }
                    this.catering.control.setValue(order_list);
                    break;
                case 'orders':
                    const orders: CateringOrder[] = this.catering.getValue();
                    const item_count = (orders || []).reduce((count, order) => count + order.item_count, 0);
                    if (item_count > 0) {
                        this.addCateringNotes().then(() => this.confirmBooking());
                    } else {
                        this.confirmBooking();
                    }
                    break;
                case 'space':
                    this.addEquipment()
                        .then(() => {
                            if (catering) {
                                this._service.navigate(['book', 'main', 'orders']);
                            } else {
                                this.confirmBooking();
                            }
                        })
                        .catch(() => {});
                    break;
                default:
                    if (space && !id) {
                        this._service.navigate(['book', 'main', 'space']);
                    } else if (this.spaces && this.spaces.length > 0) {
                        this.addEquipment()
                            .then(() => {
                                if (catering) {
                                    this._service.navigate(['book', 'main', 'orders']);
                                } else {
                                    this.confirmBooking();
                                }
                            })
                            .catch(() => {});
                    } else if ((space || (this.spaces && this.spaces.length)) && catering) {
                        this._service.navigate(['book', 'main', 'orders']);
                    } else {
                        this.confirmBooking();
                    }
                    break;
            }
        } else if (event.type === 'previous') {
            switch (this.page) {
                case 'catering':
                    if (id || !space) {
                        // Whether space selection handled on the main form
                        this._service.navigate(['book', 'main', 'orders']);
                    } else {
                        this._service.navigate(['book', 'main', 'orders']);
                    }
                    break;
                case 'orders':
                    if (id || !space) {
                        // Whether space selection handled on the main form
                        this._service.navigate(['book', 'main', 'form']);
                    } else {
                        this._service.navigate(['book', 'main', 'space']);
                    }
                    break;
                case 'space':
                    this._service.navigate(['book', 'main', 'form']);
                    break;
                default:
                    this._service.navigate(['home']);
                    break;
            }
        }
    }

    /**
     * Clear any form data
     */
    private clearForm() {
        if (localStorage) {
            localStorage.removeItem('STAFF.booking_form');
        }
        this.loadForm();
    }

    /**
     * Load old form data and intialise the form
     */
    private loadForm() {
        let booking: any = {};
        if (localStorage) {
            booking = JSON.parse(localStorage.getItem('STAFF.booking_form') || '{}');
        }
        this.form_fields = generateBookingFormMetadata(
            booking as IBooking,
            this._service.Settings.get('app.booking.fields'),
            this._service
        );
        const host = this.form_fields.find(i => i.key === 'host');
        if (host) {
            host.metadata = { service: this._service.Bookings, options: this.getHostOptions() };
            host.setValue(host.getValue() || booking.host);
        }
        this.form_fields.push(
            new ADynamicFormField({
                key: 'id',
                type: 'action',
                hide: true,
                value: booking.id
            })
        );
        this.date_field = this.form_fields
            .reduce((v, i) => v.concat(i.children && i.children.length ? i.children : [i]), [])
            .find(i => i.key === 'date');
        this.space_list = new ADynamicFormField({
            key: 'room',
            label: 'Spaces',
            type: 'action',
            format: formatSpaces,
            hide: !booking.id,
            value: booking.room || []
        });
        this.catering = new ADynamicFormField({
            key: 'catering',
            label: 'Catering',
            type: 'action',
            hide: true,
            value: (
                (booking.catering instanceof Array ? booking.catering : [booking.catering]) || []
            )
                .filter(order => order)
                .map(order => new CateringOrder(order))
        });
        this.form_fields.unshift(this.space_list);
        this.form_fields.push(this.catering);
        this.form_fields.forEach(field => {
            if (field.children && field.children.length) {
                field.children.forEach(sub_field => {
                    this.subscription(
                        sub_field.key,
                        sub_field.control.valueChanges.subscribe(value => {
                            if (value && sub_field.control.valid) {
                                this.saveForm();
                            }
                        })
                    );
                });
            } else {
                this.subscription(
                    field.key,
                    field.control.valueChanges.subscribe(value => {
                        if (value && field.control.valid) {
                            this.saveForm();
                        }
                    })
                );
            }
        });
        const empty: any = { control: { value: true, valueChanges: false }, children: [] };
        const space = (this.form_fields.find(i => i.key === 'needs_space') || empty).control.value;
        const time = this.form_fields.find(i => i.key === 'time_group') || empty;
        const all_day = this.form_fields.find(i => i.key === 'all_day') || empty;
        (time as ADynamicFormField).setDisabled(this.duration > 450);
        const start = time.children.find(i => i.key === 'start') || empty;
        this.subscription(
            'all_day_value',
            all_day.control.valueChanges.subscribe(state =>
                (start as ADynamicFormField).setDisabled(state)
            )
        );
        const id = (this.form_fields.find(i => i.key === 'id') || empty).control.value;
        this.id = id || !space ? '10' : '';
    }

    /**
     * Save changes to the form data
     */
    private saveForm() {
        this.timeout(
            'save_form',
            () => {
                const form = this.formToBooking();
                if (localStorage) {
                    localStorage.setItem('STAFF.booking_form', JSON.stringify(form));
                }
                const empty = { control: { value: true }, children: [] };
                const time = this.form_fields.find(i => i.key === 'time_group') || empty;
                (time as ADynamicFormField).children[0].setDisabled(
                    this.duration > 450 || this.all_day
                );
            },
            50
        );
    }

    /**
     * Convert form field data to a Booking object
     */
    private formToBooking(): IBooking {
        return this.form_fields.reduce((v, i) => {
            if (i.children && i.children.length) {
                i.children.forEach(j => (v[j.key] = j.control.value));
            } else {
                v[i.key] = i.control.value;
            }
            return v;
        }, {}) as IBooking;
    }

    /**
     * Show modal to user for adding equipment details to a booking
     */
    private addEquipment() {
        return new Promise((resolve, reject) => {
            const empty = { control: { value: '' } };
            const notes = this.form_fields.find(i => i.key === 'equipment');
            const expected_attendees = this.form_fields.find(i => i.key === 'expected_attendees');
            const spaces = this.form_fields.find(i => i.key === 'room');
            const cost_code = this.form_fields.find(i => i.key === 'equipment_code');
            this._service.Overlay.openModal(
                'booking-equipment',
                {
                    cmp: BookingEquipmentDetailsModalComponent,
                    data: {
                        spaces: (spaces || empty).control.value || [],
                        expected_attendees: (expected_attendees || empty).control.value || {},
                        notes: (notes || empty).control.value,
                        cost_code: (cost_code || empty).control.value
                    }
                },
                event => {
                    if (event.type === 'finish') {
                        if (notes) {
                            notes.setValue(event.data.notes);
                        }
                        if (expected_attendees) {
                            expected_attendees.setValue(event.data.expected_attendees || {});
                        }
                        if (cost_code) {
                            cost_code.setValue(event.data.cost_code);
                        }
                        resolve();
                    } else {
                        reject();
                    }
                    event.close();
                }
            );
        });
    }

    /**
     * Show modal for adding details to catering
     */
    private addCateringNotes() {
        return new Promise((resolve, reject) => {
            const empty = { control: { value: '' } };
            const notes = this.form_fields.find(i => i.key === 'catering_notes');
            const spaces = this.form_fields.find(i => i.key === 'room');
            const cost_code = this.form_fields.find(i => i.key === 'catering_code');
            this._service.Overlay.openModal(
                'booking-catering-notes',
                {
                    cmp: BookingCateringDetailsModalComponent,
                    data: {
                        spaces: (spaces || empty).control.value || [],
                        notes: (notes || empty).control.value,
                        cost_code: (cost_code || empty).control.value
                    }
                },
                event => {
                    if (event.type === 'finish') {
                        if (notes) {
                            notes.setValue(event.data.notes);
                        }
                        if (cost_code) {
                            cost_code.setValue(event.data.cost_code);
                        }
                        resolve();
                    } else {
                        reject();
                    }
                    event.close();
                }
            );
        });
    }

    /**
     * Open modal for confirming booking details
     */
    private confirmBooking() {
        this.timeout(
            'confirm-booking',
            () => {
                const fields: any = this.formToBooking();
                fields.catering = fields.catering.map((order: CateringOrder) => order.toJSON(true));
                this._service.Overlay.openModal(
                    'confirm-booking',
                    {
                        cmp: BookingDetailsModalComponent,
                        data: fields
                    },
                    event => {
                        if (event.type === 'finish') {
                            // Booking completed successfully
                            this.clearForm();
                            this._service.navigate(['schedule'], {
                                host: fields.host
                                    ? fields.host.email
                                    : this._service.Users.current().email
                            });
                            event.close();
                        } else if (event.type === 'new') {
                            // User wishes to create a new booking
                            this.clearForm();
                            this._service.navigate(['book', 'main', 'form']);
                        } else if (event.type === 'edit') {
                            // User wishes to edit the booking
                            this._service.navigate(['book', 'main', 'form']);
                            event.close();
                        } else {
                            event.close();
                        }
                    }
                );
            },
            100
        );
    }

    /**
     * Get list of users available to select as the meeting host
     */
    private getHostOptions(): IUser[] {
        const user = this._service.Users.current();
        const list: IUser[] = [user];
        if (user.delegates) {
            for (const email of user.delegates) {
                const delegate = this._service.Users.item(email);
                if (delegate) {
                    list.push(delegate);
                } else {
                    this.timeout(`load_user|${email}`, () =>
                        this._service.Users.show(email, { update: true }).then(() => {
                            this.timeout('update_host_options', () => {
                                const host = this.form_fields.find(i => i.key === 'host');
                                if (host) {
                                    host.metadata = {
                                        service: this._service.Bookings,
                                        options: this.getHostOptions()
                                    };
                                    host.setValue(host.getValue());
                                }
                            });
                        })
                    );
                }
            }
        }
        return list;
    }
}
