
import { Component, OnInit, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';

import { AppService } from '../../../../../services/app.service';
import { BaseComponent } from '../../../../../shared/globals/base.component';
import { IRoom } from '../../../../../services/data/rooms.service';
import { CateringCategory } from '../../../../../services/data/catering/catering-category.class';

@Component({
    selector: 'catering-category-list',
    templateUrl: './category-list.component.html',
    styleUrls: ['./category-list.component.scss']
})
export class BookingMainFlowCateringCategoryListComponent extends BaseComponent implements OnInit {
    /** Space selected to add catering to */
    @Input() public space: IRoom;
    /** List of available catering item categories */
    @Input() public types: CateringCategory[];
    /** Whether booking is within the catering exclusion period */
    @Input() public exclude: boolean;
    /** Emitter for the selected category */
    @Output() public selected = new EventEmitter<CateringCategory>();

    public packages: CateringCategory;

    constructor(private _service: AppService) {
        super();
    }

    public ngOnInit(): void {
    }

    public selectItem(item: CateringCategory) {
        this.selected.emit(item);
    }
}
