
import { Component, Input, Output, EventEmitter } from '@angular/core';
import { ADynamicFormField } from '@acaprojects/ngx-dynamic-forms';

import { BaseComponent } from '../../../../../shared/globals/base.component';
import { IRoom } from '../../../../../services/data/rooms.service';
import { CateringCategory } from '../../../../../services/data/catering/catering-category.class';
import { FormControl } from '@angular/forms';

@Component({
    selector: 'catering-item-list',
    templateUrl: './item-list.component.html',
    styleUrls: ['./item-list.component.scss']
})
export class BookingMainFlowCateringItemListComponent extends BaseComponent {
    /** Space selected to add catering to */
    @Input() public space: IRoom;
    /** Selected category of items to display */
    @Input() public category: CateringCategory;
    /** Whether booking is within the catering exclusion period */
    @Input() public exclude: boolean;
    /** Field for storing ordered catering items */
    @Input() public items: FormControl;
    /** Step value for adding catering items */
    @Input() public step: number = 1;
    /** Event emitter for item listings */
    @Output() public event = new EventEmitter();

}

