import { Component, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { ADynamicFormField } from '@acaprojects/ngx-dynamic-forms';
import { Router } from '@angular/router';

import { BaseComponent } from '../../../../../shared/globals/base.component';
import { CateringOrder } from '../../../../../services/data/catering/catering-order.class';
import { IRoom } from '../../../../../services/data/rooms.service';

@Component({
    selector: 'booking-main-flow-catering-orders',
    templateUrl: './order-list.component.html',
    styleUrls: ['./order-list.component.scss']
})
export class CateringOrderListComponent extends BaseComponent implements OnChanges {
    /** Form field for storing catering data for the booking */
    @Input() public catering: ADynamicFormField;
    /** List of rooms selected for the booking */
    @Input() public spaces: IRoom[];
    /** Start time of the current booking */
    @Input() public date: number;
    /** Emitter for changes to the active catering order */
    @Output('order') public active_order = new EventEmitter();
    /** Emitter for user events on component */
    @Output() public event = new EventEmitter();

    constructor(private _router: Router) {
        super();
    }

    /** List of orders in the current booking */
    public get order_list(): CateringOrder[] {
        if (!this.catering) {
            return [];
        }
        return this.catering.control.value || [];
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.date && this.catering) {
            this.catering.control.setValue(
                this.order_list.map(
                    order => new CateringOrder({ ...order, booking_date: this.date })
                )
            );
            this.order_list.forEach(
                order => (order.error = !this.spaces.find(space => space.id === order.location_id))
            );
        }
    }

    /**
     * Create new order and start editing it
     */
    public addOrder(): void {
        const order = new CateringOrder({
            id: `order-${Math.floor(Math.random() * 999_999_999)}`,
            location_id: this.spaces[0].id,
            location: this.spaces[0].name,
            booking_date: this.date,
            delivery_time: 0
        });
        this.active_order.emit(order);
        this._router.navigate(['book', 'main', 'catering'], {
            queryParams: { order_id: order.id }
        });
    }

    /**
     * Start editing the given order
     * @param order
     */
    public editOrder(order: CateringOrder): void {
        const the_order = this.order_list.find(o => o.id === order.id);
        this.active_order.emit(the_order);
        this._router.navigate(['book', 'main', 'catering']);
    }

    /**
     * Remove the given order from the catering order list
     * @param order
     */
    public deleteOrder(order: CateringOrder): void {
        const order_list = this.order_list;
        const the_order = order_list.find(o => o.id === order.id);
        order_list.splice(order_list.indexOf(the_order), 1);
        this.catering.control.setValue(order_list);
    }
}
