import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { ADynamicFormField } from '@acaprojects/ngx-dynamic-forms';

import { IBookingFlowEvent } from '../../main-flow.component';
import { BaseComponent } from '../../../../../shared/globals/base.component';
import { AppService } from '../../../../../services/app.service';
import { IRoom } from '../../../../../services/data/rooms.service';
import { CateringOrder } from '../../../../../services/data/catering/catering-order.class';
import { CateringCategory } from '../../../../../services/data/catering/catering-category.class';
import { CateringItem } from '../../../../../services/data/catering/catering-item.class';
import { FormControl } from '@angular/forms';

export interface ICateringOrderList {
    [room_id: string]: ICateringOrder;
}

export interface ICateringOrder {
    room_id: string;
    name?: string;
    items: ICateringItem[];
    total: number;
    count?: number;
    symbol?: string;
}

export interface ICateringItem {
    id: string;
    name?: string;
    amount: number;
    unit_cost: number;
    maximum?: number;
    minimum?: number;
}

@Component({
    selector: 'catering-order',
    templateUrl: './catering-order.component.html',
    styleUrls: ['./catering-order.component.scss']
})
export class BookingMainFlowCateringOrderComponent extends BaseComponent {
    /** List of rooms selected for the booking */
    @Input() spaces: IRoom[];
    /** Form field for storing catering data for the booking */
    @Input() order: CateringOrder;
    /** Form field for storing catering data for the booking */
    @Input() items: FormControl;
    /** Show contents of the order */
    @Input() public show: boolean;
    /** Step value for adding catering items */
    @Input() public step: number = 1;
    /** Whether booking is within the catering exclusion period */
    @Input() public exclude: { [space_id: string]: boolean };
    /** Emitter for form events */
    @Output() public event = new EventEmitter<IBookingFlowEvent>();

    constructor(private _service: AppService) {
        super();
    }

    /** Whether catering order is valid */
    public get valid(): boolean {
        if (!this.items) return true;
        const list = this.items.value || [];
        return list.reduce((v, i) => v && (i instanceof CateringCategory ? i.items : []).reduce((v, j) => v && this.isValid(j), true), true);
    }

    /**
     * Whether the given catering item/package is valid
     * @param item
     */
    private isValid(item) {
        if (!this.items) return true;
        if (item.must_select && item.amount) {
            return item.must_select <= item.items.reduce((v, i) => v + (i.selected ? 1 : 0), 0);
        } else if (item.items) {
            return item.items.reduce((v, i) => v && this.isValid(i), true);
        }
        return true;
    }

    /** Number of total items in all room orders */
    public get count(): number {
        if (!this.items) return 0;
        const list = this.items.value || [];
        return list.reduce((total, item) => total + item.amount, 0);
    }

    /** Total cost for all room orders */
    public get total() {
        if (!this.items) return 0;
        const list = this.items.value || [];
        return list.reduce((total, item) => total + item.total, 0);
    }

    public get symbol(): string {
        if (!this.order) return 'USD';
        const space = this._service.Rooms.list(true).find(room => room.id === this.order.location_id);
        const building = this._service.Buildings.get(space.level.bld_id) || {} as any;
        return building.currency || 'USD';
    }

    public change(item: CateringItem, amount: number) {
        if (amount < 0) {
            item.removeFromOrder(amount);
        } else {
            item.addToOrder(amount);
        }
        this.items.setValue(this.items.value);
    }
}
