import { Component, Input, EventEmitter, Output, OnChanges, SimpleChanges } from '@angular/core';
import { ADynamicFormField } from '@acaprojects/ngx-dynamic-forms';

import { IBookingFlowEvent } from '../main-flow.component';
import { IRoom } from '../../../../services/data/rooms.service';
import { BaseComponent } from '../../../../shared/globals/base.component';
import { AppService } from '../../../../services/app.service';
import { IBuilding } from '../../../../services/data/buildings.service';

import * as dayjs from 'dayjs';
import { CateringCategory } from '../../../../services/data/catering/catering-category.class';
import { CateringOrder } from '../../../../services/data/catering/catering-order.class';
import { generateCateringOrderFormFields } from '../../../../services/data/catering/catering.utilities';
import { FormGroup } from '@angular/forms';
import { Identity } from '../../../../shared/utilities/types.interfaces';

@Component({
    selector: 'booking-main-flow-catering',
    templateUrl: './catering.component.html',
    styleUrls: ['./catering.component.scss']
})
export class BookingMainFlowCateringComponent extends BaseComponent implements OnChanges {
    /** Booking ID */
    @Input() id: string;
    /** List of rooms selected for the booking */
    @Input() spaces: IRoom[];
    /** Start time of the current booking */
    @Input() all_day = false;
    /** Start time of the current booking */
    @Input() date: number;
    /** Length of the current booking in minutes */
    @Input() duration: number;
    /** Form field for storing catering data for the booking */
    @Input() order: CateringOrder;
    /** Emitter for form events */
    @Output() public event = new EventEmitter<IBookingFlowEvent>();

    /** Mapping of rooms to loading states */
    public loading: { [id: string]: boolean } = {};
    /** Mapping of room ids to available menu categories */
    public menu_map: { [room_id: string]: CateringCategory[] } = {};
    /** Selected menu category */
    public category: CateringCategory;
    /** Value to filter menu items on */
    public search_str: string;
    /** Active room to add catering items to */
    public active_space: IRoom;

    public show_order: boolean;
    /** State of modifier keys */
    public key_states: { shift: boolean; control: boolean } = { shift: false, control: false };
    /** Step value for adding catering items */
    public step: number = 1;
    /** Form fields for the order */
    public order_form: FormGroup;
    /** List of available booking times */
    public delivery_times: Identity[];

    public get space_index(): number {
        return Math.max(
            0,
            (this.spaces || []).findIndex(
                space => space.id === this.order_form.controls.location_id.value
            )
        );
    }

    public get time_index(): number {
        return Math.max(
            0,
            (this.delivery_times || []).findIndex(
                block => block.id === this.order_form.controls.delivery_time.value
            )
        );
    }

    constructor(private _service: AppService) {
        super();
    }

    public get active_building(): IBuilding {
        if (this.active_space && this.active_space.level) {
            const building = this._service.Buildings.get(this.active_space.level.bld_id);
            return building || ({} as any);
        }
        return {} as any;
    }

    /** Whether catering can be provided if no menu is available */
    public get is_catered() {
        return !!this.active_building.has_catering;
    }

    /** Whether catering is allowed for the current time */
    public get catering_allowed(): boolean {
        if (this.delivery_times[0].id < 0) { return false; }
        if ((this.menu_map[this.active_space.id] || []).find(category => category.order_anytime)) {
            return true;
        }
        return !this.within_exclusion_period;
    }

    /** Whether booking time is within the building's exlusion period for catering  */
    public get within_exclusion_period(): boolean {
        if (!this.active_building.catering_restricted_from) {
            return false;
        }
        return this.isExcluded(this.active_building.catering_restricted_from);
    }

    public isExcluded(restriction_time: number): boolean {
        const now = dayjs().startOf('m');
        const start = dayjs(this.date)
            .startOf('m')
            .second(1);
        let end = start.subtract(restriction_time || -900, 'h');
        if (end.format('ddd') === 'Sun' || end.format('ddd') === 'Sat') {
            end = end.subtract(2, 'd');
        }
        return now.isAfter(end, 's');
    }

    public get exclusion_map(): { [key: string]: boolean } {
        const exclusions: { [key: string]: boolean } = {};
        const building_list = this._service.Buildings.list();
        for (const space of this.spaces) {
            const bld = building_list.find(bld => bld.id === space.level.bld_id);
            if (bld) {
                exclusions[space.id] = this.isExcluded(bld.catering_restricted_from);
            }
        }
        return exclusions;
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.spaces && this.spaces) {
            if (
                !this.active_space ||
                this.spaces.findIndex(i => i.id === this.active_space.id) < 0
            ) {
                this.active_space = this.spaces[0];
            }
            this.loadMenu();
        }
        if (changes.date || changes.duration) {
            this.delivery_times = this.generateAvailableTimes();
        }
        if (changes.order && this.order) {
            this.order_form = generateCateringOrderFormFields(this.order);
        }
    }

    /**
     * Load menu categories for the selected rooms
     */
    public loadMenu() {
        if (this.spaces && this.spaces instanceof Array) {
            this.spaces.forEach(space => {
                this.loading[space.id] = true;
                this.menu_map[space.id] = null;
                this._service.Menu.query({ room_id: space.id }).then(
                    list => {
                        this.loading[space.id] = false;
                        this.menu_map[space.id] = (list || []) as CateringCategory[];
                    },
                    () => {
                        this.loading[space.id] = false;
                        this.menu_map[space.id] = [];
                    }
                );
            });
        }
    }

    /**
     * Update the active space
     * @param space New active space
     */
    public setSpace(space: IRoom) {
        this.category = null;
        this.order_form.controls.location_id.setValue(space.id);
        this.order_form.controls.items.setValue([]);
        this.order.storePendingChanges('location', space.name);
        this.timeout('set_space', () => (this.active_space = space), 100);
    }

    /**
     * Filter category items
     */
    public search() {
        if (this.active_space && this.search_str) {
            const search = this.search_str.toLowerCase();
            const categories = this.menu_map[this.active_space.id];
            const list = {
                id: 'search',
                name: 'Search results',
                src: '',
                package: false,
                zones: [],
                items: categories.reduce((v, i) => {
                    v = v.concat(
                        i.items.filter((j: any) => {
                            if (j.items) {
                                return (
                                    j.name.toLowerCase().indexOf(search) >= 0 ||
                                    j.items.findIndex(
                                        k => k.name.toLowerCase().indexOf(search) >= 0
                                    ) >= 0
                                );
                            }
                            return j.name.toLowerCase().indexOf(search) >= 0;
                        })
                    );
                    return v;
                }, [])
            };
            this.category = new CateringCategory(list);
        } else {
            this.category = null;
        }
    }

    /** Enable a step modifier key */
    public enableModifer(mod: 'shift' | 'control') {
        this.key_states[mod] = true;
        this.updateStep();
    }

    /** Disable a step modifier key */
    public disableModifer(mod: 'shift' | 'control') {
        this.key_states[mod] = false;
        this.updateStep();
    }

    /** Update the increment and decrement step value */
    public updateStep() {
        this.step = this.key_states.shift ? 100 : this.key_states.control ? 10 : 1;
    }

    /** Generate list of available delivery times */
    public generateAvailableTimes(): Identity[] {
        const time_list: Identity[] = [];
        if (this.date) {
            let start = dayjs(this.date).startOf('m');
            const old_start = start;
            let end = start.add(this.all_day ? 24 * 60 : this.duration, 'm');
            if (start.isBefore(start.hour(7).minute(0), 'm') || this.all_day) {
                start = start.hour(7).minute(0);
                const now = dayjs().startOf('m');
                if (start.isBefore(now, 'm')) {
                    start = now.minute(Math.ceil(now.minute() / 5) * 5);
                }
            }
            start = start.minute(Math.ceil(start.minute() / 5) * 5);
            if (end.isAfter(start.hour(20).minute(0), 'm') || this.all_day) {
                end = start.hour(20).minute(0);
            }
            let duration = Math.abs(old_start.diff(start, 'm'));
            while (start.isBefore(end, 'm')) {
                time_list.push({ id: duration, name: start.format('h:mm A') });
                start = start.add(5, 'm');
                duration += 5;
            }
            if (time_list.length <= 0) {
                time_list.push({ id: -(90 * 24 * 60), name: 'Outside of hours' });
            }
            console.log('Time list:', time_list);
        }
        return time_list;
    }
}
